html {
  font-size: 62.5%;
}

html,
body {
  height: 100%;
  font-family: "Inter", sans-serif;
}

body {
  font-size: 1.6em;
}

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
}

.logo {
  width: 100%;
  max-width: 8rem;
}
@media (min-width: 1280px) {
  .logo {
    max-width: 12rem;
  }
}

.hide {
  display: none;
}

table {
  width: 100%;
}
table thead {
  display: none;
}
table tbody {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  font-size: 1.4em;
  color: #82310C;
}
table tr {
  display: flex;
  flex-direction: column;
  width: 100%;
  background: #FFF0D3;
  padding: 2rem;
}
@media (min-width: 768px) {
  table {
    width: 100%;
    text-align: center;
    border-collapse: separate;
    border-spacing: 0.5rem;
  }
  table thead {
    background-color: #FF7F0A;
    display: table-header-group;
  }
  table th {
    background-color: #FF7F0A;
  }
  table tbody {
    display: table-row-group;
  }
  table tr {
    display: table-row;
  }
  table th {
    padding: 2rem;
    color: #FFFFFF;
    font-size: 1.2em;
  }
  table td {
    background-color: #FFF0D3;
    padding: 2rem;
    color: #82310C;
    font-size: 1em;
    border: 1px solid #FFC26D;
  }
}

.boton {
  background-color: #FFDCA5;
  padding: 15px 4rem;
  color: #82310C;
  font-size: 1em;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5em;
}
.boton:hover {
  background-color: #FFC26D;
  cursor: pointer;
}
.boton.disable {
  background-color: #FFDCA5;
  padding: 15px 4rem;
  color: #A1390B;
  font-size: 1em;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5em;
}
.boton.disable:hover {
  background-color: "";
  cursor: pointer;
}
.boton.disable:hover {
  cursor: not-allowed;
}
.boton.xl {
  font-size: 2em;
}
.boton--inline {
  width: auto;
  display: inline-block;
}
.boton.danger {
  background-color: #eb06a1;
}
.boton--secundary {
  background-color: #FF6600;
  color: #FFFFFF;
}
.boton--secundary:hover {
  background-color: #ff8533;
}

.form {
  width: 100%;
  max-width: 60rem;
}
.form .input {
  width: 100%;
  margin-bottom: 3rem;
  position: relative;
}
.form .input input {
  width: 100%;
  border: none;
  padding: 2.5rem;
  border-radius: 1.5rem;
  box-shadow: 2px 6px 32px -11px #FF7F0A;
  -webkit-box-shadow: 2px 6px 32px -11px #FF7F0A;
  -moz-box-shadow: 2px 6px 32px -11px #FF7F0A;
}
.form .input input::placeholder {
  font-size: 2.5rem;
}
.form .input input:focus {
  border: 0.2rem solid #A1390B;
  outline: none;
}
.form .input span {
  font-size: 3rem;
  position: absolute;
  right: 1rem;
  top: 30%;
  color: #CC4902;
}

.container {
  display: flex;
  height: 100%;
  flex-direction: column;
}
@media (min-width: 1280px) {
  .container {
    flex-direction: row;
  }
}

.container__content {
  height: 100%;
}

.sidebar {
  background-color: #82310C;
  width: 100%;
  height: 100%;
  animation: active 1s forwards;
  display: none;
}
.sidebar__title {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 2rem 0;
}
.sidebar__links {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 3rem;
}
.sidebar__link {
  display: flex;
  align-items: center;
  gap: 3rem;
  background-color: #FFDCA5;
  padding: 1rem;
  border-radius: 1rem;
  text-decoration: none;
  transition: all 0.3s ease;
}
.sidebar__link:hover {
  background-color: #FF9D32;
}
.sidebar__link:hover .sidebar__link__title,
.sidebar__link:hover .sidebar__icon {
  color: #FFFFFF;
}
.sidebar__link.selected {
  background-color: #FF9D32;
}
.sidebar__link.selected .sidebar__link__title,
.sidebar__link.selected .sidebar__icon {
  color: #FFFFFF;
}
.sidebar__icon {
  display: flex;
  color: #A1390B;
}
.sidebar__icon span {
  font-size: 3rem;
}
.sidebar__link__title {
  color: #A1390B;
}
.sidebar__close {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 3rem;
}
.sidebar__close__link {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.sidebar__close__text {
  color: #FFF0D3;
  font-size: 1.2em;
}
.sidebar__close span {
  color: #FFF0D3;
}
.sidebar.active {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}
@media (min-width: 1280px) {
  .sidebar {
    max-width: 30rem;
    display: block;
  }
  .sidebar__image {
    max-width: 12rem;
  }
}

.content {
  flex: 1;
  overflow: auto;
}

.close__icon {
  padding: 3rem;
  color: #FFDCA5;
  text-align: right;
  cursor: pointer;
}
.close__icon span {
  font-size: 5rem;
}
@media (min-width: 1280px) {
  .close__icon {
    display: none;
  }
}

.header--movile {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem;
  background-color: #A1390B;
}
.header--movile .material-symbols-outlined {
  color: #FFF0D3;
  font-size: 6rem;
}
.header--movile span {
  cursor: pointer;
}
@media (min-width: 1280px) {
  .header--movile {
    display: none;
  }
}

.content {
  padding: 2rem;
}

@keyframes active {
  0% {
    width: 50%;
  }
  100% {
    width: 100%;
  }
}
.login {
  display: grid;
  height: 100vh;
  grid-template-columns: 1fr;
  grid-template-rows: 200px 1fr;
}
.login__image {
  width: 100%;
  height: 100%;
  background-image: url("../img/loginBase.png");
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;
  flex: 1;
}
.login__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #FFF0D3;
}
.login__content h1 {
  font-size: 5em;
  font-weight: 700;
  color: #FF6600;
}
@media (min-width: 480px) {
  .login {
    grid-template-columns: 1.5fr 2fr;
    grid-template-rows: 1fr;
    padding: 1rem;
  }
}

.statistics {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  padding: 1rem;
  background: #FFF0D3;
}
.statistics__row {
  display: grid;
  gap: 2rem;
}
.statistics__row > * {
  background-color: rgba(255, 255, 255, 0.6117647059);
  border-radius: 2rem;
  padding: 2rem;
}
.statistics__image__container img {
  max-width: 100%;
  width: 15rem;
}
.statistics__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.statistics__info__container h2,
.statistics__info__container p {
  text-align: center;
  color: #FF6600;
}
.statistics__info__container h2 {
  font-size: 4em;
}
.statistics__info__container p {
  font-size: 1.5em;
}
@media (min-width: 1280px) {
  .statistics {
    padding: 6rem;
    grid-template-rows: 1fr 1fr;
  }
  .statistics__row {
    grid-template-rows: 1fr;
  }
  .statistics__row.col2 {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .statistics__row.col3 {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.users {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.users__header__container {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .users__header__container {
    flex-direction: row;
    align-items: center;
  }
}
.users__title, .users__desc {
  text-align: center;
  color: #CC4902;
}
.users__title {
  font-size: 5em;
  margin: 0;
}
.users__actions {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.users__actions__buttons {
  display: flex;
  justify-content: flex-end;
  column-gap: 2rem;
}
.users__input {
  width: 100%;
  background-color: #FFF0D3;
  border: none;
  padding: 2rem 6rem;
  color: #82310C;
}
.users__input:focus {
  outline: 1px solid #FFC26D;
}
.users .search {
  position: relative;
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  transition: all 0.3s ease-in-out;
}
.users .search__icon {
  position: absolute;
  left: 2rem;
  top: 30%;
}
@media (min-width: 768px) {
  .users {
    gap: 1rem;
  }
  .users__title, .users__desc {
    text-align: left;
  }
}

.search.active__search {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}